const UserGuard = ({
  currentUser,
  allowUserList = [],
  excludeUserList = [],
  children
}) => {
  // If currentUser is in the exclude list, block it
  if (excludeUserList.includes(currentUser)) return null;
 
  // If allowUserList is provided, only allow users in that list
  if (allowUserList.length > 0 && !allowUserList.includes(currentUser)) return null;
 
  // Otherwise render children
  return <>{children}</>;
};

export default UserGuard