import { useEffect, useState } from "react";
import { motion } from "framer-motion";

// ======== THEME LIST =========
const themeConfigs = {
  "01": { name: "Ocean Blue", "--bs-primary1": "#23627e", "--bs-primary2": "#f8fafc", "--bs-primary3": "rgb(23, 59, 75)", "--bs-nav": "#1e293b", "--bs-border-primary1": "#fff" },
  "02": { name: "Night Navy", "--bs-primary1": "#1c2b3f", "--bs-primary2": "#f8fafc", "--bs-primary3": "#37495f", "--bs-nav": "#0b1e3d", "--bs-border-primary1": "#fff" },
  "03": { name: "Graphite", "--bs-primary1": "#2f2f2f", "--bs-primary2": "#f0f0f0", "--bs-primary3": "#5a5a5a", "--bs-nav": "#2a2a2a", "--bs-border-primary1": "#fff" },
  "04": { name: "Royal Indigo", "--bs-primary1": "#191a3b", "--bs-primary2": "#e0fff0", "--bs-primary3": "#3a3c6f", "--bs-nav": "#191a3b", "--bs-border-primary1": "#fff" },
  "05": { name: "Sky Soft", "--bs-primary1": "#87ceeb", "--bs-primary2": "#00000", "--bs-primary3": "#b0dfff", "--bs-nav": "#87ceeb", "--bs-border-primary1": "#000000" },
  "06": { name: "Purple King", "--bs-primary1": "#4b0082", "--bs-primary2": "#f4f4f9", "--bs-primary3": "#6a0dad", "--bs-nav": "#2e0854", "--bs-border-primary1": "#fff" },
  "07": { name: "Emerald", "--bs-primary1": "#004d40", "--bs-primary2": "#e0f2f1", "--bs-primary3": "#00695c", "--bs-nav": "#00332a", "--bs-border-primary1": "#fff" },
  "08": { name: "Chocolate", "--bs-primary1": "#6d4c41", "--bs-primary2": "#fbe9e7", "--bs-primary3": "#8d6e63", "--bs-nav": "#4e342e", "--bs-border-primary1": "#fff" },
  "09": { name: "Red Flame", "--bs-primary1": "#b71c1c", "--bs-primary2": "#ffebee", "--bs-primary3": "#d32f2f", "--bs-nav": "#7f0000", "--bs-border-primary1": "#fff" },
  "10": { name: "Grass Green", "--bs-primary1": "#1b5e20", "--bs-primary2": "#e8f5e9", "--bs-primary3": "#2e7d32", "--bs-nav": "#0d3311", "--bs-border-primary1": "#fff" },
  "11": { name: "Royal Blue", "--bs-primary1": "#0d47a1", "--bs-primary2": "#e3f2fd", "--bs-primary3": "#1565c0", "--bs-nav": "#002171", "--bs-border-primary1": "#fff" },
  "12": { name: "Lavender", "--bs-primary1": "#512da8", "--bs-primary2": "#ede7f6", "--bs-primary3": "#673ab7", "--bs-nav": "#311b92", "--bs-border-primary1": "#fff" },
  "13": { name: "Ash Gray", "--bs-primary1": "#37474f", "--bs-primary2": "#eceff1", "--bs-primary3": "#455a64", "--bs-nav": "#263238", "--bs-border-primary1": "#fff" },
  "14": { name: "Orange Sunset", "--bs-primary1": "#ff5722", "--bs-primary2": "#fff3e0", "--bs-primary3": "#ff7043", "--bs-nav": "#bf360c", "--bs-border-primary1": "#fff" },
  "15": { name: "Stone Blue", "--bs-primary1": "#607d8b", "--bs-primary2": "#eceff1", "--bs-primary3": "#78909c", "--bs-nav": "#37474f", "--bs-border-primary1": "#fff" },
  "16": { name: "Deep Indigo", "--bs-primary1": "#283593", "--bs-primary2": "#e8eaf6", "--bs-primary3": "#3949ab", "--bs-nav": "#1a237e", "--bs-border-primary1": "#fff" },
  "17": { name: "Aqua Wave", "--bs-primary1": "#00838f", "--bs-primary2": "#e0f7fa", "--bs-primary3": "#0097a7", "--bs-nav": "#006064", "--bs-border-primary1": "#fff" },
  "18": { name: "Rose Pink", "--bs-primary1": "#c2185b", "--bs-primary2": "#fce4ec", "--bs-primary3": "#d81b60", "--bs-nav": "#880e4f", "--bs-border-primary1": "#fff" },
  "19": { name: "Olive Gold", "--bs-primary1": "#827717", "--bs-primary2": "#f9fbe7", "--bs-primary3": "#9e9d24", "--bs-nav": "#616200", "--bs-border-primary1": "#fff" },
  "20": { name: "Brown Wood", "--bs-primary1": "#795548", "--bs-primary2": "#efebe9", "--bs-primary3": "#8d6e63", "--bs-nav": "#4e342e", "--bs-border-primary1": "#fff" },
  "21": { name: "Classic Light", "--bs-primary1": "#ffffffff", "--bs-primary2": "#000000ff", "--bs-primary3": "#f5f3f2ff", "--bs-nav": "#e8dad6ff", "--bs-border-primary1": "#fc9d00" },
};

export default function ThemeSelector() {
  const [selectedTheme, setSelectedTheme] = useState(localStorage.getItem("themeCode") || "21");

  useEffect(() => {
    const theme = themeConfigs[selectedTheme];
    if (theme) {
      Object.entries(theme).forEach(([key, value]) => {
        if (key.startsWith("--")) {
          document.documentElement.style.setProperty(key, value);
        }
      });
    }
  }, [selectedTheme]);

  const handleThemeChange = (code) => {
    localStorage.setItem("themeCode", code);
    setSelectedTheme(code);
  };

  return (
  <div
    style={{
      display: "grid",
      gridTemplateColumns: "repeat(auto-fit, minmax(120px, 1fr))", // <-- update
      gap: "14px",
    }}
  >
    {Object.entries(themeConfigs).map(([code, theme]) => (
      <motion.div
        key={code}
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.97 }}
        onClick={() => handleThemeChange(code)}
        style={{
          border: selectedTheme === code ? "3px solid var(--bs-nav)" : "1px solid #666",
          borderRadius: "12px",
          overflow: "hidden",
          cursor: "pointer",
          textAlign: "center",
          position: "relative",
          background: "rgba(15,23,42,0.6)",
          boxShadow: "0 4px 12px rgba(0,0,0,0.25)",
        }}
      >

        {selectedTheme === code && (
          <motion.div
            initial={{ opacity: 0, scale: 0.4 }}
            animate={{ opacity: 1, scale: 1 }}
            style={{
              position: "absolute",
              top: 6,
              right: 6,
              width: 24,
              height: 24,
              borderRadius: "50%",
              background: "var(--bs-nav)",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              color: "white",
              fontSize: 14,
              fontWeight: "bold",
            }}
          >
            
          </motion.div>
        )}

        {/* VERTICAL 3 COLORS */}
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "1fr 1fr 1fr",
            height: "80px",
            width: "100%",
          }}
        >
          <div style={{ background: theme["--bs-primary1"] }} />
          <div style={{ background: theme["--bs-primary2"] }} />
          <div style={{ background: theme["--bs-primary3"] }} />
        </div>

        <div style={{ padding: "6px" }}>
          <div style={{ fontWeight: 600, fontSize: "0.85rem" }}>{theme.name}</div>
        </div>
      </motion.div>
    ))}
  </div>
);

}
